import React, {Suspense, lazy} from 'react'
import { Route, BrowserRouter as Router, Switch } from 'react-router-dom'
import {SwitchTransition, CSSTransition } from 'react-transition-group'
import './css/transitions.css'
import profile from './images/profile.jpg'

import Container from './components/Container'
import LoadingPage from './components/LoadingPage'

const Home = lazy(() => import('./pages/Home'))
const About = lazy(() => import('./pages/About'))
const Projects = lazy(() => import('./pages/Projects'))
const ErrorPage = lazy(() => import('./pages/ErrorPage'))
const Hire = lazy(() => import('./pages/Hire'))

function App() {
  let lang = 'en'
  const userLangs = navigator.languages
  for (let i=0; i <= userLangs.length; i++) {
    if (userLangs[i].search('es') !== -1) {
      lang = 'es'
      break
    } else if (userLangs[i].search('en') !== -1) {break}
  }

  return (
    <div className="App">
      <img src={profile} alt="profile" className="backdrop"/>
      <Router>
        <Route render={({location}) => (
          <SwitchTransition>
            <CSSTransition 
              classNames="focus" 
              timeout={250} 
              key={location.key ? location.key : "initial"}
              unmountOnExit>
              <Container lang={lang}>
                <Suspense fallback={<LoadingPage/>}>
                  <Switch location={location}>
                    <Route exact path="/about/">
                      <About lang={lang}/>
                    </Route>
                    <Route exact path="/projects/">
                      <Projects lang={lang}/>
                    </Route>
                    <Route exact path="/hire/">
                      <Hire lang={lang}/>
                    </Route>
                    <Route exact path="/">
                      <Home lang={lang}/>
                    </Route>
                    <Route path="*">
                      <ErrorPage lang={lang}/>
                    </Route>
                  </Switch>
                </Suspense>
              </Container>
            </CSSTransition>
          </SwitchTransition>
        )}/>
      </Router>
    </div>
  );
}

export default App;
