import React from 'react'
import profile from '../images/profile.jpg'
import '../css/nav.css'
import {Link} from 'react-router-dom'

export default function Nav({lang, dark}) {
    const text = {en:['About','Projects','Hire Me!'], es:['Sobre mí','Proyectos','¡Contrátame!']}

    return (
        <nav className={(dark ? "darkmode" : null)}>
            <Link className="logocont" to="/" >
                <img src={profile} alt="Profile" className="logo" />
                <h1>frank</h1>
            </Link>
            <div className="buttons">
                <Link to="/about" className='button'>
                    {text[lang][0]}
                </Link>
                <Link to="/projects/" className='button'>
                    {text[lang][1]}
                </Link>
                <Link to='/hire/' className='button hirebtn'>
                    {text[lang][2]}
                </Link>
                <p className="lang">{lang.toUpperCase()}</p>
            </div>
        </nav>
    )
}
